.fez-connection-status {
	display: none !important;
}

.fez-connection-status-pending {
	color: #ff0000;
	border: 1px solid #ff0000;
	width: fit-content;
	padding: 5px;
	border-radius: 5px;
}

.fez-connection-status-connected {
	color: #008000;
	border: 1px solid #008000;
	width: fit-content;
	padding: 5px;
	border-radius: 5px;
}

.fez-delivery-order-details {
	display: flex;
	flex-direction: column;
	gap: 5px;
	background: #f0f0f1;
	padding: 15px 20px;

	h3 {
		display: flex;
		align-items: center;
		gap: 10px;
		margin: 0 !important;

		img {
			width: 40px;
			height: 40px;
		}

		span {
			font-size: 14px;
			font-weight: 600;
			color: #000;
		}
	}

	p {
		font-size: 14px;
		font-weight: 400;
		color: #000;
		margin: 0;
	}
}

.fez-delivery-order-buttons {
	display: flex;
	justify-content: space-between;
	gap: 10px;
}

.fez-delivery-order-sync-button-container {
	display: flex;
	background: #fff;
	color: #000;
	padding: 2px 10px;
	border-radius: 5px;
	cursor: pointer;
	transition: all 0.3s ease;
	border: 1px solid #000;
	align-items: center;
	gap: 10px;
	width: fit-content;
	margin-top: 25px;

	&:hover {
		background: #bbe7b1;
		color: #000;
		border: 1px solid #000;
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
	}

	.fez-delivery-logo {
		width: 30px;
		height: 26px;
	}

	.fez-delivery-order-sync-button {
		text-decoration: none;
		font-size: 11px;
		font-weight: 600;
		display: flex;
		align-items: center;
		gap: 10px;
		color: #272727;
	}
}

.fez-delivery-order-details-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
